@font-face {
  font-family: "poppins";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/poppins-regular-webfont.woff2") format("woff2"), url("../fonts/poppins-regular-webfont.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "poppins";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/poppins-medium-webfont.woff2") format("woff2"), url("../fonts/poppins-medium-webfont.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "poppins";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/poppins-bold-webfont.woff2") format("woff2"), url("../fonts/poppins-bold-webfont.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "cormorantgaramond";
  font-style: italic;
  font-weight: 400;
  src: url("../fonts/cormorantgaramond-italic-webfont.woff2") format("woff2"), url("../fonts/cormorantgaramond-italic-webfont.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "cormorantgaramond";
  font-style: normal;
  font-weight: 600;
  src: url("../fonts/cormorantgaramond-semibold-webfont.woff2") format("woff2"), url("../fonts/cormorantgaramond-semibold-webfont.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "cormorantgaramond";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/cormorantgaramond-bold-webfont.woff2") format("woff2"), url("../fonts/cormorantgaramond-bold-webfont.woff") format("woff");
  font-display: swap;
}
html {
  box-sizing: border-box;
  font-size: 10px;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: "poppins", "Arial", sans-serif;
  font-size: 1.6rem;
  line-height: 130%;
  font-weight: 400;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  min-height: 100vh;
  min-width: 320px;
  background-color: #ffffff;
  color: #1d1613;
}

.visually-hidden {
  position: absolute;
  clip: rect(0, 0, 0, 0);
  clip-path: inset(100%);
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  overflow: hidden;
}

.list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.button {
  border: none;
  outline: none;
}

.link {
  display: block;
  position: relative;
  text-decoration: none;
  color: inherit;
}

.link,
button {
  cursor: pointer;
}

main {
  outline: 1px dashed red;
  outline-offset: -1px;
}

.container {
  margin-right: auto;
  margin-left: auto;
  max-width: 1260px;
}
@media (min-width: 1024px) {
  .container {
    width: 92.24%;
    padding-left: 45px;
    padding-right: 45px;
  }
}
@media (max-width: 1023px) {
  .container {
    width: 92.1875%;
    padding-left: 15px;
    padding-right: 15px;
  }
}
@media (max-width: 767px) {
  .container {
    width: 90.625%;
    padding-left: 0;
    padding-right: 0;
  }
}

.scroll-stop {
  overflow: hidden;
}

.page-navigation ul {
  display: flex;
}
.page-navigation li:not(:last-child) {
  margin-right: 2%;
  padding-right: 10px;
}
.page-navigation .link {
  opacity: 0.7;
}
.page-navigation .link:hover, .page-navigation .link:focus {
  text-decoration: underline;
}
.page-navigation .link:active {
  opacity: 1;
}

.general-information {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
@media (min-width: 1024px) {
  .general-information {
    font-family: inherit;
    font-style: inherit;
    font-weight: inherit;
    font-size: inherit;
    line-height: inherit;
  }
}

.header {
  font-family: "poppins", "Arial", sans-serif;
  font-size: 1.4rem;
  line-height: 150%;
  font-weight: 500;
  color: inherit;
}
@media (min-width: 1024px) {
  .header {
    background-color: #ffffff;
  }
}
@media (max-width: 1023px) {
  .header {
    color: #ffffff;
    background-color: #a87b62;
  }
}
@media (max-width: 1023px) {
  .header--js {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    background-color: #ffffff;
    z-index: 1;
  }
}
@media (max-width: 1023px) {
  .header--js .header__container--lower {
    display: none;
  }
}
@media (max-width: 1023px) {
  .header--js .header__logo {
    --icon-color: #a87b62;
  }
}
@media (max-width: 1023px) {
  .header--js .header__burger,
.header--js .header__cart {
    --icon-color: #1d1613;
  }
}
@media (max-width: 1023px) {
  .header--js.menu-open {
    height: 100vh;
    background-color: #a87b62;
    z-index: 1;
  }
}
@media (max-width: 1023px) {
  .header--js.menu-open .header__container--lower {
    display: block;
    height: 85%;
    overflow-y: auto;
    position: absolute;
    top: 126px;
    left: 50%;
    transform: translateX(-50%);
  }
}
@media (max-width: 767px) {
  .header--js.menu-open .header__container--lower {
    top: 91px;
  }
}
@media (max-width: 1023px) {
  .header--js.menu-open .header__burger,
.header--js.menu-open .header__logo,
.header--js.menu-open .header__cart {
    --icon-color: #ffffff;
  }
}
.header a {
  border-bottom: 1px dashed transparent;
}
.header a:hover, .header a:focus {
  border-color: inherit;
}
.header a:active {
  opacity: 0.5;
}
@media (max-width: 1023px) {
  .header .header__cart {
    border: none;
  }
}

.header__container--upper {
  display: flex;
  align-items: flex-start;
  flex-wrap: wrap;
  padding-top: 30px;
}
@media (min-width: 1024px) {
  .header__container--upper {
    margin-bottom: 19px;
  }
}
@media (max-width: 1023px) {
  .header__container--upper {
    margin-bottom: 30px;
  }
}
@media (max-width: 767px) {
  .header__container--upper {
    padding-top: 20px;
    margin-bottom: 20px;
  }
}
@media (min-width: 1024px) {
  .header__container--upper > *:not(.header__logo):not(.header__form-box) {
    transform: translate(0, 13px);
  }
}
@media (min-width: 1024px) {
  .header__container--lower {
    width: 100%;
    max-width: 100%;
    padding-left: 0;
    padding-right: 0;
    margin-left: 0;
    margin-right: 0;
    border-top: 1px solid #f6f5f4;
  }
}
@media (max-width: 1023px) {
  .header__container--lower {
    padding-top: 10px;
  }
}
@media (max-width: 767px) {
  .header__container--lower {
    padding-top: 10px;
  }
}

.header__form-box {
  position: relative;
}
@media (min-width: 1024px) {
  .header__form-box {
    width: 12.6984%;
    width: 140px;
    margin-left: 29px;
    margin-right: 32.8%;
    transform: translate(0, 10px);
  }
}
@media (max-width: 1023px) {
  .header__form-box {
    margin-bottom: 47px;
  }
}
@media (max-width: 767px) {
  .header__form-box {
    margin-bottom: 37px;
  }
}
.header__form-box svg {
  position: absolute;
  left: 0;
  top: 50%;
  transform: translate(-28px, -50%);
  fill: var(--icon-color);
}
@media (min-width: 1024px) {
  .header__form-box svg {
    --icon-color: #000000;
  }
}
@media (max-width: 1023px) {
  .header__form-box svg {
    --icon-color: #ffffff;
    transform: translate(13px, -50%) scale(1.15);
  }
}
.header__form-box input {
  font-family: "poppins", "Arial", sans-serif;
  font-size: 1.4rem;
  line-height: 150%;
  font-weight: 500;
  width: 100%;
  border-color: transparent;
  outline: none;
  background-color: transparent;
  padding-left: 0;
  padding-right: 0;
}
@media (min-width: 1024px) {
  .header__form-box input {
    border-width: 1px;
    border-style: dashed;
  }
}
@media (max-width: 1023px) {
  .header__form-box input {
    color: #ffffff;
    height: 40px;
    padding-left: 30px;
    padding-right: 30px;
    background-color: rgba(255, 255, 255, 0.15);
    border-radius: 21px;
  }
}
@media (min-width: 1024px) {
  .header__form-box input:hover, .header__form-box input:focus {
    border-bottom-color: #000000;
  }
}
@media (max-width: 1023px) {
  .header__form-box input:hover, .header__form-box input:focus {
    box-shadow: 0 0 3px #ffffff;
  }
}
.header__form-box input::placeholder {
  font-size: inherit;
  line-height: inherit;
}

.header__logo {
  margin-right: auto;
  fill: --icon-color;
}
@media (min-width: 1024px) {
  .header__logo {
    --icon-color: #a87b62;
  }
}
@media (max-width: 1023px) {
  .header__logo {
    --icon-color: #ffffff;
  }
}
@media (max-width: 767px) {
  .header__logo {
    width: 52px;
    height: 52px;
  }
}

@media (min-width: 1024px) {
  .header__login {
    margin-right: 3.1%;
  }
}
@media (max-width: 1023px) {
  .header__login {
    font-size: 1.8rem;
    line-height: 130%;
    width: max-content;
    margin-bottom: 74px;
  }
}
@media (max-width: 767px) {
  .header__login {
    margin-bottom: 42px;
  }
}

@media (min-width: 1024px) {
  .header__cart {
    margin-right: 2px;
  }
}
@media (max-width: 1023px) {
  .header__cart {
    --icon-color: #ffffff;
    height: 18px;
    transform: translate(1px, 24px);
  }
}
@media (max-width: 767px) {
  .header__cart {
    transform: translate(1px, 17px);
  }
}
@media (max-width: 1023px) {
  .header__cart:hover svg, .header__cart:focus svg {
    transform: scale(1.1);
  }
}
@media (max-width: 1023px) {
  .header__cart:active {
    opacity: 0.5;
  }
}

@media (min-width: 1024px) {
  .header__navigation {
    margin-right: auto;
    margin-left: auto;
    width: 493px;
    padding-top: 18px;
    padding-bottom: 8px;
    transform: translate(5px, 0);
  }
}
.header__navigation ul {
  display: flex;
  align-items: flex-start;
  flex-wrap: wrap;
}
@media (min-width: 1024px) {
  .header__navigation ul {
    width: 100%;
  }
}
@media (max-width: 1023px) {
  .header__navigation ul {
    margin-bottom: 40px;
  }
}
@media (max-width: 767px) {
  .header__navigation ul {
    margin-bottom: 30px;
  }
}
@media (max-width: 1023px) {
  .header__navigation ul {
    flex-direction: column;
    align-items: flex-start;
  }
}
@media (min-width: 1024px) {
  .header__navigation li {
    margin-bottom: 10px;
  }
}
@media (min-width: 1024px) {
  .header__navigation li:not(:last-child) {
    margin-right: 7.0994%;
  }
}
@media (max-width: 1023px) {
  .header__navigation li:not(:last-child) {
    margin-right: 0;
    margin-bottom: 20px;
  }
}
.header__navigation a {
  font-size: 1.6rem;
  line-height: 150%;
}
@media (max-width: 1023px) {
  .header__navigation a {
    font-size: 2.4rem;
  }
}

@media (max-width: 1023px) {
  .header__burger {
    --icon-color: #ffffff;
    margin-right: 39.7%;
    background-color: transparent;
    transform: translate(-6px, 23px);
  }
}
@media (max-width: 767px) {
  .header__burger {
    margin-right: 28.2%;
    transform: translate(-6px, 16px);
  }
}
@media (max-width: 1023px) {
  .header__burger:hover svg, .header__burger:focus svg {
    transform: scale(1.1);
  }
}
@media (max-width: 1023px) {
  .header__burger:active {
    opacity: 0.5;
  }
}

@media (max-width: 1023px) {
  .header__general-information {
    font-size: 1.8rem;
    line-height: 130%;
    color: rgba(255, 255, 255, 0.75);
    margin-bottom: 40px;
  }
}
@media (max-width: 767px) {
  .header__general-information {
    margin-bottom: 32px;
  }
}
@media (max-width: 1023px) {
  .header__general-information li:not(:last-child) {
    margin-bottom: 20px;
  }
}

.footer {
  font-family: "poppins", "Arial", sans-serif;
  font-size: 1.6rem;
  line-height: 130%;
  font-weight: 500;
  color: #ffffff;
  background-color: #000000;
}
.footer .container {
  display: flex;
  align-items: flex-start;
  flex-wrap: wrap;
}
@media (min-width: 1024px) {
  .footer .container {
    padding-top: 96px;
    padding-bottom: 94px;
  }
}
@media (max-width: 1023px) {
  .footer .container {
    padding-top: 60px;
    padding-bottom: 60px;
  }
}
.footer p {
  margin: 0;
  padding: 0;
}
@media (min-width: 1024px) {
  .footer li:not(:last-child) {
    margin-bottom: 21px;
  }
}
@media (max-width: 1023px) {
  .footer li:not(:last-child) {
    margin-bottom: 25px;
  }
}
.footer a {
  border-bottom: 1px dashed transparent;
}
.footer a:hover, .footer a:focus {
  border-color: inherit;
}
.footer a:active {
  opacity: 0.5;
}

.footer__subscription {
  padding-right: 10px;
  margin-right: auto;
}
@media (min-width: 1024px) {
  .footer__subscription {
    width: 30%;
  }
}
@media (min-width: 1170px) {
  .footer__subscription {
    width: 43.7%;
  }
}

.footer__leading-paragraph-box {
  font-family: "cormorantgaramond", "Arial", sans-serif;
  font-size: 3.4rem;
  line-height: 130%;
  font-weight: 700;
  letter-spacing: 0.025em;
}
@media (min-width: 1024px) {
  .footer__leading-paragraph-box {
    margin-bottom: 4px;
    transform: translate(0, -6px);
  }
}
@media (max-width: 767px) {
  .footer__leading-paragraph-box {
    margin-bottom: 30px;
  }
}

@media (min-width: 1024px) {
  .footer__auxiliary-paragraph-box {
    margin-bottom: 30px;
  }
}
@media (max-width: 767px) {
  .footer__auxiliary-paragraph-box {
    margin-bottom: 50px;
  }
}

.footer__form-box {
  position: relative;
}
.footer__form-box input {
  font-family: "poppins", "Arial", sans-serif;
  font-size: 1.4rem;
  line-height: 150%;
  font-weight: 500;
  width: 100%;
  border-color: transparent;
  outline: none;
  background-color: transparent;
  color: #ffffff;
  padding: 14px 0 6px;
  border-bottom: 1px solid #ffffff;
}
.footer__form-box input:hover, .footer__form-box input:focus {
  border-bottom-style: dashed;
}
.footer__form-box input::placeholder {
  font-size: inherit;
  line-height: inherit;
}

.footer__submit {
  --icon-color: #ffffff;
  font-size: inherit;
  line-height: inherit;
  color: inherit;
  margin: 0;
  padding: 0;
  position: absolute;
  top: 56%;
  right: 0;
  transform: translate(0, -50%);
  background-color: transparent;
}
.footer__submit span {
  font-size: inherit;
  line-height: inherit;
  color: inherit;
  margin-right: 12px;
}
.footer__submit:hover, .footer__submit:focus {
  --icon-color: #eaab90;
  color: #eaab90;
}
.footer__submit:active {
  opacity: 0.5;
}

.footer__buyers-information {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
@media (min-width: 1024px) {
  .footer__buyers-information {
    margin-right: 5%;
  }
}

.footer__general-information {
  padding-left: 10px;
}
@media (min-width: 1024px) {
  .footer__general-information {
    margin-right: auto;
  }
}
@media (min-width: 1170px) {
  .footer__general-information {
    margin-right: 8%;
  }
}

.footer__social-networks {
  --icon-color: #ffffff;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
.footer__social-networks li {
  position: relative;
}
@media (min-width: 1024px) {
  .footer__social-networks li {
    padding-left: 45px;
  }
}
.footer__social-networks a:hover svg, .footer__social-networks a:focus svg {
  transform: translate(-30px, -50%) scale(1.2);
}
.footer__social-networks a:active {
  opacity: 0.5;
}
.footer__social-networks svg {
  position: absolute;
  left: 0;
  top: 50%;
  transform: translate(-30px, -50%);
}
@media (min-width: 1024px) {
  .footer__social-networks svg {
    margin-right: 15px;
  }
}
@media (min-width: 1024px) {
  .footer__social-networks svg.facebook-icon {
    transform: translate(-28px, -50%);
  }
}

.hidden-entity {
  display: none;
}

@media (max-width: 1023px) {
  .hidden-before-desktop {
    display: none;
  }
}

@media (min-width: 1024px) {
  .hidden-on-desktop {
    display: none;
  }
}

@media (max-width: 767px) {
  .hidden-before-tablet {
    display: none;
  }
}

@media (min-width: 768px) {
  .hidden-after-mobile {
    display: none;
  }
}

@media (max-width: 767px) {
  .hidden-on-mobile {
    display: none;
  }
}